.lista-artigo {
  display: flex;
  flex-direction: column;
  gap: 48px;


.blog-artigo {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  gap: 16px;
  padding-bottom: 48px;
  border-bottom: #dadada solid 1px;

  @include md {
    flex-direction: row;
    gap: 32px;
    align-items: flex-start;
  }

  .autor-infos {
    .autor-img {
      width: 250px;
      height: 250px;

      @include md {
        width: 200px;
        height: 200px;
      }
    }

    @include md {
      min-width: 200px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .autor-nome {
      font-weight: 700;
      text-align: center;
      font-size: 24px;
      font-family: $montserrat;
      color: $orange;
      text-align: center;
      margin-top: 16px;
      
    }

    .autor-cargo {
      font-weight: 700;
      text-align: center;
    }
  }

  .blog-info {
    display: flex;
    flex-direction: column;
    gap: 16px;

    .blog-noticia {
      font-family: $montserrat;
      font-weight: 700;
      font-size: 24px;
    }
  }
}

}